.fade-enter-active {
  transition: opacity .5s;
}
.fade-leave-active {
  transition: opacity 2s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
#message {
  position: absolute;
  top: 49px;
  left: 0;
  right: 0;
  z-index: 20;
}
.close-notification {
  float: right;
  position: absolute;
  top: 0;
  right: 1em;
  cursor: pointer;
}
